import React from 'react'
import * as classes from './ConsumerItem.module.scss'

export default function ConsumerItem(props) {
  console.log(props.OrderPlaced)
  return (
    <div className={classes.ConsumerItem}>
        <p>{props.OrderPlaced[1]}<span>{props.OrderPlaced[2]}</span></p>
        <p>{props.OrderNo}</p>
        <p>{props.StoreLocation}</p>
        <p>{props.Customer[1]}<span>{props.Customer[2]}</span></p>
        <p>{props.Value}</p>
    </div>
  )
}
